<template>
  <div id="enterpriseHome">
    <div id="left">
      <div id="location">
        当前位置：<router-link
          to="/home"
          class="selectable"
        >服务平台</router-link> > <span class=blue>企业</span>
      </div>
      <div id="enterpriseNews">
        <div>
          <i
            class="fa fa-align-justify"
            aria-hidden="true"
          ></i> 企业新闻
          <div
            class="more"
            @click.stop="routerLinkName('listPage',1,'pageNum','企业新闻','type')"
          >更多</div>
        </div>
        <div
          class="loading"
          v-if="!message"
        >data is loading...please wait</div>
        <div
          id="enterpriseNewsPics"
          v-if="message"
        >
          <div
            id="picsSelect"
            @click.stop
          >
            <button
              v-for="(i,index) in message.companyNews"
              class="selectButton selectable"
              :key="index"
              v-bind:class="{activeButton:play==0?play1==index:play2==index}"
              @click.stop="nextPic(index)"
            >{{index + 1}}</button>
          </div>
          <transition
            name="slide1"
            enter-active-class="slideInRight"
            leave-active-class="slideOutLeft"
          >
            <div
              v-if="play==0"
              class="newsPicFrame"
              @click.stop="routerLinkName('enterpriseNews',message.companyNews[play1].id,'id','企业新闻','type')"
            >
              <img
                class="newsPic selectable"
                v-if="message.companyNews[play1].img"
                v-bind:src="message.companyNews[play1].img || newsPics"
              >
              <div
                class="noPic selectable"
                v-if="!message.companyNews[play2].img"
              >
                <span>{{message.companyNews[play1].title}}</span>
              </div>
              <br>
              <div class="newsText">{{message.companyNews[play1].title}}</div>
            </div>
          </transition>
          <transition
            name="slide2"
            enter-active-class="slideInRight"
            leave-active-class="slideOutLeft"
          >
            <div
              v-if="play==1"
              class="newsPicFrame"
              @click.stop="routerLinkName('enterpriseNews',message.companyNews[play2].id,'id','企业新闻','type')"
            >
              <img
                class="newsPic selectable"
                v-if="message.companyNews[play2].img"
                v-bind:src="message.companyNews[play2].img || newsPics"
              >
              <div
                class="noPic selectable"
                v-if="!message.companyNews[play2].img"
              >
                <span>{{message.companyNews[play2].title}}</span>
              </div>
              <br>
              <div class="newsText">{{message.companyNews[play2].title}}</div>
            </div>
          </transition>
        </div>
      </div>
      <div id="enterpriseDisplay">
        <div
          @click="routerLink('/enterprise/enterpriseHall')"
          class="more"
        ></div>
        <div class="smallTitle">
          <i
            class="fa fa-picture-o"
            aria-hidden="true"
          ></i> 品牌展厅
        </div>
        <div
          class="loading"
          v-if="!message"
        >data is loading...please wait</div>
        <div
          class="flexbox"
          v-if="message"
        >
          <div
            class="displayBox selectable"
            v-for="(i,index) in message.companyBrand"
            :key="index"
            @click.stop="routerLinkName('enterpriseDisplay',i.id,'id')"
          >
            <img
              v-bind:src="i.logo"
              class="displayPic"
            >
            <br>
            <span class="displayName">{{i.name}}</span>
          </div>
        </div>
      </div>
      <div id="memberState">
        <div class="smallTitle">
          <i
            class="fa fa-users"
            aria-hidden="true"
          ></i> 会员动态
          <div
            class="more"
            @click.stop="routerLinkName('listPage',1,'pageNum','会员动态','type')"
          >更多</div>
        </div>
        <div
          class="loading"
          v-if="!message"
        >data is loading...please wait</div>
        <div
          id="memberBox"
          class="flexbox"
          v-if="message"
        >
          <div
            class="state"
            v-for="(i,index) in message.companyDynamics"
            :key="index"
            @click.stop="routerLinkName('enterpriseNews',i.id,'id','会员动态','type')"
          >
            {{i.title}}
          </div>
        </div>
      </div>
      <div id="memberList">
        <div class="smallTitle">
          <i
            class="fa fa-users"
            aria-hidden="true"
          ></i> 会员列表
          <div class="more">更多</div>
        </div>
        <div
          class="loading"
          v-if="!message"
        >data is loading...please wait</div>
        <div
          id="memberBox"
          class="flexbox"
          v-if="message"
        >
          <div
            class="state"
            v-for="(i,index) in memberList"
            :key="index"
          >
            {{i.enterpriseName}}
          </div>
        </div>
      </div>
      <br>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>

<script>
import Vue from "Vue";
import axios from "axios";
import enterpriseRight from "@/components/enterprise/enterpriseRight";
import { getMemberList } from "@/api/register.js";

export default {
  name: "enterpriseHome",
  data() {
    return {
      message: undefined,
      theParams: undefined,
      newsPics: "./static/PICS/noEnterpriseNews.jpg",
      enterpriseHot: [
        "新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办"
      ],
      memberList: "",
      getBrand: undefined,
      getCompanyNews: undefined,
      play: 0,
      play1: 0,
      play2: 1
    };
  },
  components: {
    enterpriseRight
  },
  methods: {
    routerLink: function(path) {
      this.$router.push(path);
    },
    routerLinkName: function(
      theName,
      paramValue,
      paramName,
      param2Value,
      param2Name
    ) {
      this.params = this.params || new Object();
      this.params[paramName] = paramValue;
      if (param2Name) {
        this.params[param2Name] = param2Value;
      }
      this.$router.push({ name: theName, params: this.params });
    },
    nextPic: function(num) {
      //num表示要播放的，如果没有就表示下一张
      clearTimeout(this.newsInterval);
      this.newsInterval = setTimeout(this.nextPic, 2333);
      if (this.play == 0) {
        if (num == this.play1) return false;
        this.play2 =
          num !== undefined
            ? num
            : (this.play1 + 1) % this.message.companyNews.length;
      } else {
        if (num == this.play2) return false;
        this.play1 =
          num !== undefined
            ? num
            : (this.play2 + 1) % this.message.companyNews.length;
      }
      this.play = (this.play + 1) % 2;
    }
  },
  created: function() {
    if (Vue.prototype.wmessage) this.message = Vue.prototype.wmessage;
    this.axios.get(this.HOST + "/common/getIndex").then(res => {
      Vue.prototype.wmessage = res.data.message;
      this.message = res.data.message;
      setTimeout(this.nextPic(), 2333);
    });
  },
  mounted: function() {
    let that = this;
    getMemberList(that)
      .then(res => {
        if (res) {
          that.memberList = res.message;
        }
      })
      .catch(err => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "获取会员信息异常",
          duration: 2000
        });
      });
  }
};
</script>
<style lang="scss" scoped>
* {
  padding: 0;
  margin: 0;
}
@mixin leftBorder {
  padding: 5px;
  width: 700px;
  margin-bottom: 10px;
  background: rgba(255, 255, 255, 0.8);
}
@mixin picBig {
  width: 700px;
  height: 395px;
}
.flexbox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background: white;
}
.smallTitle {
  margin-bottom: 5px;
}
div.loading {
  width: 100%;
  height: 100%;
  z-index: 999;
  background: white;
  text-align: center;
}
div#enterpriseNews,
div#enterpriseDisplay,
div#memberState,
div#memberList {
  @include leftBorder;
  position: relative;
}
div#enterpriseNews {
  height: 420px;
  position: relative;
  overflow: hidden;
  background: white;
}
div#picsSelect {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 35px;
  z-index: 999;
}
button.selectButton {
  background: white;
  color: black;
  margin: 0px 4px;
  border: 0;
  width: 1.2rem;
  height: 1.2rem;
  font-size: 0.8rem;
  border-radius: 3px;
  outline: none;
  opacity: 0.88;
}
button.activeButton {
  transition: 666ms;
  background: black;
  color: white;
}
div#enterpriseNewsPics {
  @include picBig;
  overflow: hidden;
  white-space: nowrap;
  position: absolute;
  left: 5px;
  bottom: 5px;
}
div.newsPicFrame {
  position: absolute;
  bottom: 0px;
  left: 0px;
  @include picBig;
  overflow: hidden;
}
img.newsPic {
  @include picBig;
}
div.noPic {
  position: relative;
  @include picBig;
  background: rgb(156, 156, 156);
  transition: 123ms;
}
div.noPic > span {
  position: relative;
  display: block;
  top: 50%;
  left: 5%;
  transform: translateY(-50%);
  font-size: 2rem;
  width: 90%;
  white-space: pre-wrap;
  text-align: center;
}
div.newsText {
  position: absolute;
  width: 690px;
  padding-left: 10px;
  bottom: 0px;
  color: white;
  background: rgba(0, 0, 0, 0.66);
  height: 28px;
  line-height: 28px;
}
div.displayBox {
  width: 128px;
  margin: 20px 10px;
  text-align: center;
  white-space: normal;
}
img.displayPic {
  width: 128px;
  height: 72px;
  margin: 20px 10px;
}
div#memberBox {
  background: rgba(0, 0, 0, 0);
  justify-content: space-around;
}
.state {
  background: white;
  width: 690px;
  padding: 5px;
  cursor: pointer;
  position: relative;
}
.state:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 5px;
  width: 680px;
  border-bottom: 1px solid;
  opacity: 0.2;
}
.state:hover {
  color: #127de7;
}
.slideInRight {
  animation-duration: 500ms;
}
.slideOutLeft {
  animation-duration: 500ms;
}
</style>



