<!-->会员注册页面<-->
<template>
  <div id="second">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="品牌" prop="brand">
        <el-input v-model="ruleForm.brand"></el-input>
      </el-form-item>
      <el-form-item label="版本" prop="version">
        <el-input v-model="ruleForm.version"></el-input>
      </el-form-item>
      <el-form-item label="应用领域" prop="applicationArea">
        <el-input v-model="ruleForm.applicationArea"></el-input>
      </el-form-item>
      <el-form-item label="应用行业" prop="applicationIndustry">
        <el-input v-model="ruleForm.applicationIndustry"></el-input>
      </el-form-item>
      <el-form-item label="应用场景" prop="applicationScenario">
        <el-input v-model="ruleForm.applicationScenario"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="nextStep('ruleForm')">跳过</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import option from '../../js/libs/registerOp.js'
import {insertRepresentativeWork} from '../../api/register.js'

export default {
  name: "second",
  data() {
    return {
      ruleForm: {
      },
      rules: {
      }
    };
  },
  created(){
    this.ruleForm = option.secondForm;
    this.rules = option.secondRules;
  },
  methods: {
    nextStep(formName) {
      this.$confirm('确认跳过？', '提示', {
        confirmButtonText: '继续',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$emit('changeStep',2)
      })
    },
    submitForm(formName) {
      let that = this
      that.$confirm("确认信息无误？", "提示", {
        confirmButtonText: "继续",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let data = Object.assign({}, that.ruleForm);
        console.log(data);
        that.$refs[formName].validate(valid => {
          if (valid) {
            insertRepresentativeWork(data)
              .then(res => {
                if(res.result =="success"){
                  that.$refs[formName].resetFields();
                  that.$message({
                    showClose: true,
                    dangerouslyUseHTMLString: true,
                    message:
                      "<p>提交成功,你可以继续添加代表作品或点击跳过进入下一步</p>",
                    type: "success",
                    duration: 5000
                  });
                  }
                  else{
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>提交失败,请检查表单是否有误</p>",
              type: "error",
              duration: 3000
            });
          }
        });
      });
    }
  }
};
</script>


<style lang="scss" scoped>
</style>



