<!-->会员注册页面<-->
<template>
  <div id="third">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="会员id" prop="enterpriseId ">
        <el-select v-model="ruleForm.enterpriseId" placeholder="请选择会员id">
          <el-option
            v-for="item in options"
            :key="item.label"
            :label="item.label"
            :value="item.label">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import option from '../../js/libs/registerOp.js'
import {insertMemberUser,getMemberList} from '../../api/register.js'

export default {
  name: "third",
  data() {
    return {
      ruleForm: {},
      rules: {},
      options: {}
    };
  },
  created() {
    let that = this;
    that.ruleForm = option.thirdForm;
    that.rules = option.thirddRules;
    // that.options = option.options;
    getMemberList()
      .then(res => {
        if (res) {
          console.log(typeof(res.message))
          that.options = res.message.map(function(item) {
            return {
              value: item.enterpriseName,
              label: item.id
            };
          });
        }
      })
      .catch(err => {
        console.log(err);
        that.$message.error({
          showClose: true,
          message: "获取会员信息异常",
          duration: 2000
        });
      });
  },
  methods: {
    submitForm(formName) {
      let that = this
      that.$confirm("确认注册？", "提示", {
        confirmButtonText: "继续",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        let data = Object.assign({}, that.ruleForm);
        console.log(data);
        that.$refs[formName].validate(valid => {
          if (valid) {
            insertMemberUser(data)
              .then(res => {
                if(res.result =="success"){
                  that.$refs[formName].resetFields();
                  that.$message({
                    showClose: true,
                    dangerouslyUseHTMLString: true,
                    message:
                      "<p>注册成功</p>",
                    type: "success",
                    duration: 2000
                  });
                }
                else{
                  that.$message({
                    showClose: true,
                    message: res.message,
                    type: "error",
                    duration: 2000
                  });
                }
              })
              .catch(err => {
                that.$message({
                  showClose: true,
                  message: err.message,
                  type: "error",
                  duration: 2000
                });
              });
          } else {
            that.$message({
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: "<p>提交失败,请检查表单是否有误</p>",
              type: "error",
              duration: 3000
            });
          }
        });
      });
    },
  }
};
</script>


<style lang="scss" scoped>
</style>



