<!-->会员注册页面<-->
<template>
  <div id="register">
    <el-steps class="steps" :active="active" simple>
      <el-step title="会员信息" icon="el-icon-tickets"></el-step>
      <el-step title="代表作品" icon="el-icon-edit-outline"></el-step>
      <el-step title="注册" icon="el-icon-upload"></el-step>
    </el-steps>
    <keep-alive>
      <user-list :is="this.currentPath" v-on:changeStep="changeStep"></user-list>
    </keep-alive>
  </div>
</template>

<script>
import First from "@/components/register/first";
import Second from "@/components/register/second";
import Third from "@/components/register/third";
export default {
  name: "register",
  components: { First, Second,Third},
  data() {
    return {
      currentPath:"first",
      active:0,
    };
  },
  methods: {
    changeStep(i){
      if(i == 0){
        this.active = 0;
        this.currentPath = "first"
      }
      if(i == 1){
        this.active = 1;
        this.currentPath = "second"
      }
      if(i == 2){
        this.active = 2;
        this.currentPath = "third"
      }
    }
  }
}
</script>


<style lang="scss" scoped>
#register{
  height: 100%;
  width: 60%;
  padding: 50px;
  margin: 30px auto;
  background: rgba(235, 240, 247, 0.932);
  border-radius: 3%;
}
.steps{
  margin-left: 30px;
  margin: 20px 0 50px 30px;
  }
</style>



