import axios from 'axios'
import context from '../pages/index/index.js'//引入全局vue对象
import Qs from "qs";

// const service = axios.create({
//   baseURL: context.HOST,
//   timeout: 200000,
// });

export function insertNewMember(data) {
  const url = '/user/insertNewMember'
  let params = Qs.stringify({ data });
  return axios({
    method: "post",
    url: context.HOST + url,
    data: params
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

